.modal {
  height: 100vh;
  width: 100vw;
  background-color: rgba(0, 0, 0, 0.6);
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  /* opacity: 0; */
  /* pointer-events: none; */
  transition: 0.5s;
  /* z-index: -10; */
  opacity: 1;
  pointer-events: all;
  z-index: 10;
}
