.order {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 520px;
  margin: 0 auto;
}
.number {
  text-shadow: 0px 0px 16px rgba(51, 51, 255, 0.25),
    0px 0px 8px rgba(51, 51, 255, 0.25), 0px 4px 32px rgba(51, 51, 255, 0.5);
  composes: text text_type_digits-large mb-8 from global;
}
.subtitle {
  composes: text text_type_main-medium mb-15 from global;
}
.image {
  composes: mb-15 from global;
}
.caption {
  composes: text text_type_main-default from global;
}
.subcaption {
  composes: text text_type_main-default from global;
  color: #8585ad;
}
